@font-face {
  font-family: Chaloops;
  src: url(./fonts/Chaloops.otf);
}

body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:'#FCFCFD';
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'Chaloops';
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
}

h2{
  font-family: Verdana;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
}

h3{
  font-family: Verdana;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
}

p{
  font-family: Verdana;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
}